import styled from 'styled-components';

export const Container = styled.div`
  max-width: 700px;
  background: #ffff;
  border-radius: 4px;

  padding: 30px;
  margin: 50px auto;
`;

export const Background = styled.div`
  max-width: auto; 
  background-image: url('https://www.hotmilhas.com.br/wp-content/uploads/2017/06/29-06-blog1.png');
  background-position: center;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: bisque;
  padding: 30px;
  margin: 50px auto;
`;
