/* eslint-disable jsx-a11y/alt-text */
import React, {useState} from 'react';
import { Container, FlexBox } from './styles';
import InputMask from 'react-input-mask';

import { Input } from '@rocketseat/unform';
export default function Home() {


  const [cnpj, setCNPJ] = useState('');
  
  function handleSubmit(event){
  
    event.preventDefault();
    const newCnpj = event.target.elements.cnpj.value;
    localStorage.setItem('@cnpj', newCnpj);

    window.location.href = '/cadastro'
  }


  return (
    <> 
    <Container>
    <div>
        <div id="top-search">
          <input placeholder="O que você deseja?" type="search" />
        </div>
      </div>
      <div id="main">
        <div className="col-12">
          <h1 className="main">Entrar</h1>
        </div>
        <div className="col">
          <p className="aviso">
            Atenção: Digite seu CNPJ para acesso. Caso não possua cadastro ativo
            lhe encaminharemos para o formulário de registro.
          </p>
        </div>
      </div>
      <FlexBox>
        <form  onSubmit={handleSubmit}>
          <div>
          <InputMask name="cnpj" mask={'999.999.999/9999-99'} maxLength="17"  value={cnpj} onChange={(e) => setCNPJ(e.target.value)} >
           {() => <Input name="cnpj" type="cnpj" required  placeholder="999.999.999/9999-99" />}   
          </InputMask> 
          </div>
          <div className="col-12">
            <button className="submit" type="submit">Entrar</button>
          </div>
        </form>
        </FlexBox>
    </Container>
  </>
  );
}
