import React from 'react';
import { useDispatch } from 'react-redux';
import InputMask from 'react-input-mask';
import { Form, Input } from '@rocketseat/unform';
import * as Yup from 'yup';
import {  Container, Background} from './styles.js';

import { signUpRequest } from '../../store/modules/auth/actions';

const schema = Yup.object().shape({
  name: Yup.string().required('Razão Social é obrigatório'),
  email: Yup.string()
    .email('Insira um e-mail válido')
    .required('E-mail é obrigatório'),
  phone: Yup.string()
    .min(11, 'No mínimo 11 caracteres')
    .required('Telefone é obrigatório'),
});


const cnpj =  localStorage.getItem('@cnpj');



export default function Signup() {

  const dispatch = useDispatch();
  function handleSubmit(data) {
    dispatch(signUpRequest(data))
  }


  return (
    <>
    <Background>
    <Container>
      <Form  schema={schema} onSubmit={handleSubmit}>
        <p>CNPJ</p>
        <input   name="cnpj"  value={cnpj} />
        <p>Razão Social</p>
        <Input name="name" type="name"  placeholder="Digite sua  razão social" />
        <p>E-mail</p>
        <Input name="email" type="email" placeholder="Digite seu E-mail" />
        <p>Telefone de Contato</p>
        <InputMask mask={'(99)-99999-9999'}  type="phone">
            {() => <Input name="phone" type="phone" placeholder="(99) - 99999-9999" />}
        </InputMask>
        <button type="submit" className="btn btn" >RESGATAR</button>
  
      </Form>
    </Container>
    </Background>
    </>
  );
}
